/* 00.Variables and Imports */
@import url("https://fonts.googleapis.com/css?family=Nunito:300,400,400i,600,700");

$main-menu-item-height: 110px;
$main-menu-width: 120px;
$main-menu-width-lg: 110px;
$main-menu-width-md: 100px;
$main-menu-width-xs: 90px;

$sub-menu-width: 230px;
$sub-menu-width-lg: 230px;
$sub-menu-width-md: 230px;
$sub-menu-width-xs: 230px;

$navbar-height: 100px;
$navbar-height-xs: 70px;
$navbar-height-md: 80px;
$navbar-height-lg: 90px;

$main-margin: 60px;
$main-margin-lg: 50px;
$main-margin-md: 40px;
$main-margin-xs: 15px;

$app-menu-width: 280px;

$main-menu-item-height-mobile: 90px;

$menu-collapse-time: 300ms;
$animation-time-long: 1000ms;
$animation-time-short: 200ms;

$border-radius: 0.1rem;
$border-radius-rounded: 0.75rem;

$theme-color-purple-monster: #922c88;
$theme-color-blue-navy: #00365a;
$theme-color-blue-yale: #145388;
$theme-color-blue-olympic: #008ecc;
$theme-color-green-moss: #576a3d;
$theme-color-green-lime: #6fb327;
$theme-color-orange-carrot: #ed7117;
$theme-color-red-ruby: #900604;
$theme-color-yellow-granola: #c0a145;
$theme-color-grey-steel: #48494b;

$info-color: #3195a5;
$success-color: #3e884f;
$warning-color: #b69329;
$error-color: #c43d4b;

/* 01.Base */
html {
    width: 100%;
    height: 100%;
    background: $background-color;
}

:root {
    --theme-color-1: #{$theme-color-1};
    --theme-color-2: #{$theme-color-2};
    --theme-color-3: #{$theme-color-3};
    --theme-color-4: #{$theme-color-4};
    --theme-color-5: #{$theme-color-5};
    --theme-color-6: #{$theme-color-6};
    --theme-color-1-10: #{rgba($theme-color-1, 0.1)};
    --theme-color-2-10: #{rgba($theme-color-2, 0.1)};
    --theme-color-3-10: #{rgba($theme-color-3, 0.1)};
    --theme-color-4-10: #{rgba($theme-color-4, 0.1)};
    --theme-color-5-10: #{rgba($theme-color-5, 0.1)};
    --theme-color-6-10: #{rgba($theme-color-6, 0.1)};

    --primary-color: #{$primary-color};
    --foreground-color: #{$foreground-color};
    --separator-color: #{$separator-color};
}

#root {
    height: 100%;
}

body {
    font-family: "Nunito", sans-serif;
    font-size: 0.8rem;
    font-weight: 400;
    color: $primary-color;
    background: $background-color;

    &.background {
        height: 100%;

        main {
            margin: 0 !important;
            height: 100%;

            .container {
                height: 100%;
            }
        }
    }
}

.rtl * {
    text-align: right;
}

.fixed-background {
    background: url("/assets/img/balloon.jpg") no-repeat center center fixed;
    background-size: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

h1 {
    font-size: 1.75rem;
    padding-bottom: 10px;
    display: inline-block;

    @include respond-below(sm) {
        font-size: 1.3rem;
        padding-top: 0.5rem;
    }
}

h2 {
    font-size: 1.4rem;

    @include respond-below(sm) {
        font-size: 1.1rem;
    }
}

h3 {
    font-size: 1.3rem;

    @include respond-below(sm) {
        font-size: 1rem;
    }
}

h3 {
    font-size: 1.2rem;

    @include respond-below(sm) {
        font-size: 1rem;
    }
}

h4 {
    font-size: 1.15rem;

    @include respond-below(sm) {
        font-size: 0.9rem;
    }
}

h5 {
    font-size: 1.1rem;

    @include respond-below(sm) {
        font-size: 0.9rem;
    }
}

h6 {
    font-size: 1rem;

    @include respond-below(sm) {
        font-size: 0.85rem;
    }
}

hr {
    border-top: 1px solid $separator-color;
}

.disable-text-selection {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

::selection {
    background: lighten($theme-color-3, 20%);
}

::-moz-selection {
    background: lighten($theme-color-3, 20%);
}

.map-item {
    height: 400px;
    width: 100%;
}

.scrollbar-container {
    margin-right: -15px;
    padding-right: 15px;
    position: relative;
}

.ps__rail-y {
    width: 5px;
}

.ps__thumb-y {
    left: 0;
    width: 5px;
    right: 0;
}

.ps__rail-y:hover > .ps__thumb-y,
.ps__rail-y:focus > .ps__thumb-y,
.ps__rail-y.ps--clicking .ps__thumb-y {
    width: 5px;
    left: 0;
}

.ps__thumb-y {
    background-color: $separator-color;
}

.video-play-icon {
    width: 100%;
    height: 100%;
    position: absolute;

    span {
        position: absolute;
        background: rgba(255, 255, 255, 0.7);
        height: 1.25em;
        border-radius: 0.75em;
        line-height: 0.65em;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 1.75em;
        width: 2em;
        text-align: center;

        &:before {
            content: "";
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 8px 0 8px 12px;
            font-size: 0;
            border-color: transparent transparent transparent $theme-color-1;
        }
    }

    &:hover,
    &:active {
        span {
            background: rgba(255, 255, 255, 0.85);
        }
    }
}

.logo-single {
    width: 110px;
    height: 35px;
    background: url($logoPath) no-repeat;
    background-position: center center;
    display: inline-block;
    margin-bottom: 60px;
}

.list-item-heading-container {
    width: calc(100% - 125px);
}

.list-item-heading {
    font-size: 1rem;
}

.truncate {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.no-transition {
    transition: none !important;
}

.display-1 {
    @include respond-below(sm) {
        font-size: 2.8rem;
    }
}

.display-2 {
    @include respond-below(sm) {
        font-size: 2.6rem;
    }
}

.display-3 {
    @include respond-below(sm) {
        font-size: 2.4rem;
    }
}

.display-4 {
    @include respond-below(sm) {
        font-size: 2.2rem;
    }
}

.lead {
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 2rem;

    @include respond-below(sm) {
        font-size: 1.1rem;
        line-height: 1.6rem;
    }
}

a {
    color: $primary-color;
    transition: color $animation-time-short;

    &:hover,
    &:active {
        text-decoration: initial;
        color: $theme-color-1;
    }
}

p {
    font-size: 0.85rem;
    line-height: 1.3rem;
    font-family: "Nunito", sans-serif;
}

.text-large {
    font-size: 1.9rem !important;
}

.text-one {
    font-size: 1rem !important;
}

.text-xlarge {
    font-size: 2.7rem !important;
}

.text-small {
    font-size: 0.76rem;
    line-height: 0.9rem;
}

.text-white {
    color: $button-text-color !important;
}

.text-extra-small {
    font-size: 0.6rem;
}

.text-default {
    color: $primary-color !important;
}

.text-muted {
    color: $muted-color !important;
}

.text-semi-muted {
    color: $secondary-color !important;
}

.font-weight-medium {
    font-weight: 500;
}

.font-weight-semibold {
    font-weight: 600;
}

.color-theme-1 {
    color: $theme-color-1;
}

.color-theme-2 {
    color: $theme-color-2;
}

.view-icon {
    font-size: 20px;
    color: $secondary-color;

    &:hover {
        color: $theme-color-1;
    }

    &.s {
        font-size: 18px;
    }
}

#displayOptions {
    a {
        cursor: pointer;
    }

    a.active i {
        color: $theme-color-1;
    }

    button {
        border-color: $secondary-color;
        color: $secondary-color;

        &:hover {
            background-color: $theme-color-1;
            border-color: $theme-color-1;
            color: $button-text-color;
        }
    }

    .btn-outline-dark:not(:disabled):not(.disabled):active,
    .btn-outline-dark:not(:disabled):not(.disabled).active,
    .show > .btn-outline-dark.dropdown-toggle {
        background-color: $theme-color-1;
        border-color: $theme-color-1;
        color: $button-text-color;
    }

    .view-icon {
        svg {
            width: 19px;
        }

        .view-icon-svg {
            fill: $secondary-color;
        }

        &:hover,
        &.active {
            .view-icon-svg {
                fill: $theme-color-1;
            }
        }
    }
}

.text-theme-2 {
    color: $theme-color-2 !important;
}

.text-theme-3 {
    color: $theme-color-3 !important;
}

.text-primary,
.text-theme-1 {
    color: $theme-color-1 !important;
}

.text-secondary {
    color: $primary-color !important;
}

.main-heading {
    border-bottom: 1px solid $separator-color;
}

.separator {
    border-bottom: 1px solid $separator-color;
}

.alert-dismissible .close {
    padding: 0.5rem 1.25rem;
}

.rounded {
    border-radius: 50px !important;
}

.img-thumbnail {
    border-radius: $border-radius;
    padding: 0;
    border: initial;
}
.img-fluid{
    width: 100%;
}
.white {
    color: $button-text-color !important;
}

.opacity-25 {
    opacity: 0.25;
}

.opacity-50 {
    opacity: 0.5;
}

.opacity-75 {
    opacity: 0.75;
}

/* 02.Buttons */

button {
    color: $primary-color;
    outline: initial !important;
}

.btn-arrow {
    display: inline-block;
    text-align: center;
    border-radius: 30px !important;
    width: 42px;
    height: 42px;
    line-height: 24px;
}

.btn-arrow i {
    font-size: 15px;
    display: inline-block;
    text-align: center;
}

.btn-sm.btn-arrow {
    width: 34px;
    height: 34px;
    line-height: 17px;
}

.btn-sm.btn-arrow i {
    font-size: 13px;
    line-height: 10px;
}

.btn {
    border-radius: 50px;
    outline: initial !important;
    box-shadow: none !important;
    box-shadow: initial !important;
    font-size: 0.8rem;
    padding: 0.5rem 1.25rem 0.5rem 1.25rem;
    transition: background-color box-shadow 0.1s linear;
}

.btn-shadow {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.15),
        0 1px 3px 1px rgba(0, 0, 0, 0.15) !important;
    transition: background-color box-shadow 0.1s linear;

    &:hover,
    &:focus {
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15),
            0 4px 6px 2px rgba(0, 0, 0, 0.15) !important;
    }
}

.btn-empty {
    background: transparent !important;
}

.btn-lg,
.btn-group-lg > .btn,
.btn-group-sm > .btn {
    border-radius: 50px;
}

.btn.default {
    border-radius: $border-radius;
}

.btn-primary {
    background-color: $theme-color-1;
    border-color: $theme-color-1;
    color: $button-text-color;

    &:hover {
        color: $button-text-color;
        background-color: darken($theme-color-1, 8%);
        border-color: darken($theme-color-1, 8%);
    }
}

.check-button {
    cursor: default !important;
}

.check-button.btn-primary {
    background-color: $theme-color-1 !important;
    border-color: $theme-color-1 !important;
    opacity: 1;
}

.check-button .custom-control {
    min-height: 1.1rem;
    margin-top: -7px;
}

.dropdown-menu {
    font-size: 0.8rem;
    border-radius: $border-radius;
    background: $input-background;
    border-color: rgba($primary-color, 0.15);
}

.dropdown-item {
    padding: 0.5rem 1.25rem;
    color: $primary-color;
}

.dropdown-item:hover,
.dropdown-item:focus {
    background-color: $background-color;
    color: $primary-color;
}

.dropdown-item.active,
.dropdown-item:active {
    color: $button-text-color;
    text-decoration: none;
    background-color: $theme-color-1;
}

.dropdown-divider {
    border-color: $separator-color;
}

.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
    background-color: darken($theme-color-1, 12%);
    border-color: darken($theme-color-1, 12%);
    color: $button-text-color;
}

.btn-secondary {
    background-color: $theme-color-2;
    border-color: $theme-color-2;
    color: $button-text-color;

    &:hover {
        color: $button-text-color;
        background-color: darken($theme-color-2, 8%);
        border-color: darken($theme-color-2, 8%);
    }
}

.btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.show > .btn-secondary.dropdown-toggle {
    background-color: darken($theme-color-2, 12%);
    border-color: darken($theme-color-2, 12%);
    color: $button-text-color;
}

.btn-primary.btn-primary-gradient {
    background: linear-gradient(
        to right,
        $theme-color-1,
        darken($theme-color-1, 15%)
    );

    &:hover {
        background: linear-gradient(
            to right,
            $theme-color-1,
            darken($theme-color-1, 8%)
        );
    }
}

.btn-primary-gradient:not(:disabled):not(.disabled):active,
.btn-primary-gradient:not(:disabled):not(.disabled).active,
.show > .btn-primary-gradient.dropdown-toggle {
    background: linear-gradient(
        to right,
        $theme-color-1,
        darken($theme-color-1, 15%)
    );
}

.btn-secondary-gradient {
    background: linear-gradient(
        to right,
        $theme-color-2,
        darken($theme-color-2, 15%)
    );

    &:hover {
        background: linear-gradient(
            to right,
            $theme-color-2,
            darken($theme-color-2, 8%)
        );
    }
}

.btn-secondary-gradient:not(:disabled):not(.disabled):active,
.btn-secondary-gradient:not(:disabled):not(.disabled).active,
.show > .btn-secondary-gradient.dropdown-toggle {
    background: linear-gradient(
        to right,
        $theme-color-2,
        darken($theme-color-2, 15%)
    );
}

.btn-warning {
    background-color: $warning-color;
    border-color: $warning-color;
}

.btn-success,
.btn-info,
.btn-danger,
.btn-warning {
    color: $button-text-color;

    &:hover {
        color: $button-text-color;
    }
}

.btn-outline-success,
.btn-outline-info,
.btn-outline-danger,
.btn-outline-warning {
    &:hover {
        color: $foreground-color;
    }
}

.btn-light {
    color: $dark-btn-background;
    background-color: $light-btn-background;
    border-color: $light-btn-background;

    &:hover {
        color: $dark-btn-background;
        background-color: darken($light-btn-background, 8%);
        border-color: darken($light-btn-background, 8%);
    }
}

.btn-dark {
    color: $light-btn-background;
    background-color: $dark-btn-background;
    border-color: $dark-btn-background;

    &:hover {
        color: $light-btn-background;
        background-color: darken($dark-btn-background, 5%);
        border-color: darken($dark-btn-background, 5%);
    }
}

.btn-outline-dark {
    color: $dark-btn-background;
    border-color: $dark-btn-background;

    &:hover {
        color: $foreground-color;
        background-color: $dark-btn-background;
        border-color: $dark-btn-background;
    }
}

.btn-outline-white {
    color: $button-text-color;
    border-color: $button-text-color;
    background-color: initial;

    &:hover {
        color: $theme-color-1;
        background-color: $button-text-color;
    }
}

.btn-outline-light {
    color: $light-btn-background;
    border-color: $light-btn-background;

    &:hover {
        color: $foreground-color;
        background-color: $light-btn-background;
        border-color: $light-btn-background;
    }
}

.btn-outline-primary {
    color: $theme-color-1;
    border-color: $theme-color-1;

    &:hover {
        color: $foreground-color;
        background-color: $theme-color-1;
        border-color: $theme-color-1;
    }
}

.btn-outline-theme-3 {
    background: unset;
    color: $theme-color-3;
    border-color: $theme-color-3;

    &:hover {
        background-color: $theme-color-3;
        border-color: $theme-color-3;
        color: $foreground-color;
    }
}

.btn-outline-primary:not(:disabled):not(.disabled):active,
.btn-outline-primary:not(:disabled):not(.disabled).active,
.show > .btn-outline-primary.dropdown-toggle {
    background-color: $theme-color-1;
    border-color: $theme-color-1;
    color: $foreground-color;
}

.btn-outline-secondary {
    color: $theme-color-2;
    border-color: $theme-color-2;

    &:hover {
        background-color: $theme-color-2;
        border-color: $theme-color-2;
        color: $foreground-color;
    }
}

.btn-outline-secondary:not(:disabled):not(.disabled):active,
.btn-outline-secondary:not(:disabled):not(.disabled).active,
.show > .btn-outline-secondary.dropdown-toggle {
    background-color: $theme-color-2;
    border-color: $theme-color-2;
    color: $foreground-color;
}

.btn-header-light {
    color: $separator-color;
    border-color: transparent;
    background: transparent;

    &:hover {
        background-color: transparent;
        border-color: $separator-color;
    }
}

.btn-header-primary {
    color: $theme-color-1;
    border-color: transparent;
    background: transparent;

    &:hover {
        background-color: transparent;
        border-color: $theme-color-1;
    }
}

.btn-header-secondary {
    color: $theme-color-2;
    border-color: transparent;
    background: transparent;

    &:hover {
        background-color: transparent;
        border-color: $theme-color-2;
    }
}

.btn-header-primary-light {
    color: $theme-color-3;
    border-color: transparent;
    background: transparent;

    &:hover {
        background-color: transparent;
        border-color: $theme-color-3;
    }
}

.btn-xl,
.btn-group-xl > .btn {
    line-height: 1.5;
    font-weight: 700;
    letter-spacing: 0.05rem;
    padding: 1rem 3.5rem 0.9rem;
}

.btn-lg,
.btn-group-lg > .btn {
    line-height: 1.5;
    font-weight: 700;
    letter-spacing: 0.05rem;
    padding: 0.6rem 2.5rem 0.6rem 2.5rem;
}

.btn-sm,
.btn-group-sm > .btn {
    padding: 0.45rem 1rem 0.45rem 1rem;
    font-size: 0.8rem;
    line-height: 1.5;
}

.btn-xs,
.btn-group-xs > .btn {
    padding: 0.25rem 0.75rem 0.25rem 0.75rem;
    font-size: 0.76rem;
    line-height: 1.3;
}

.btn-primary.disabled,
.btn-primary:disabled {
    background-color: $theme-color-1;
    border-color: $theme-color-1;
    color: $foreground-color;
}

.btn-secondary.disabled,
.btn-secondary:disabled {
    background-color: $theme-color-2;
    border-color: $theme-color-2;
    color: $foreground-color;
}

.btn-group > .btn:not(:last-child):not(.dropdown-toggle),
.btn-group > .btn-group:not(:last-child) > .btn {
    border-top-right-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
}

.btn-group > .btn:not(:first-child),
.btn-group > .btn-group:not(:first-child) > .btn {
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
}

.btn-link {
    color: $theme-color-1;
}

.btn-link:hover {
    color: rgba($theme-color-1, 0.8);
    text-decoration: underline;
}

.white-underline-link {
    color: $button-text-color;
    text-decoration: underline;

    &:hover,
    &:active {
        color: $button-text-color;
        text-decoration: initial;
    }
}

.btn-multiple-state {
    position: relative;
    transition: opacity 500ms;

    .spinner,
    .icon {
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        opacity: 0;
        visibility: hidden;
        top: 50%;
        transform: translateY(-50%);
        transition: opacity 500ms;
        color: $button-text-color;
    }

    .icon i {
        vertical-align: text-bottom;
        font-size: 18px;
    }

    .label {
        transition: opacity 500ms;
    }

    &.show-spinner {
        .label {
            opacity: 0;
        }

        .spinner {
            opacity: 1;
            visibility: visible;
        }
    }

    &.show-success {
        .label {
            opacity: 0;
        }

        .icon.success {
            opacity: 1;
            visibility: visible;
        }
    }

    &.show-fail {
        .label {
            opacity: 0;
        }

        .icon.fail {
            opacity: 1;
            visibility: visible;
        }
    }

    &.btn-primary:disabled {
        opacity: 1;
        background: darken($theme-color-1, 10%);
        border-color: darken($theme-color-1, 10%);
    }

    &.btn-secondary:disabled {
        opacity: 1;
        border-color: darken($theme-color-2, 10%);
    }
}

.icon-button {
    padding: 0;
    font-size: 14px;
    width: 34px;
    height: 34px;
    line-height: 34px;

    &.large {
        width: 44px;
        height: 44px;
        font-size: 18px;
    }

    &.small-icon {
        font-size: 12px;
        line-height: 32px;
    }
}

.top-right-button-single {
    width: unset;

    @include respond-below(md) {
        width: calc(100%);
    }
}

.top-right-button-container {
    float: right;
    position: relative;

    .btn-group .btn {
        line-height: 1;
    }

    @include respond-below(xs) {
        float: initial;
        display: flex;
        justify-content: space-between;
    }

    .top-right-button {
        @include respond-below(xs) {
            display: flex;
            flex-grow: 1;
            justify-content: center;
            align-items: center;
            margin-right: 5px;
        }
    }

    .dropdown {
        @include respond-below(xs) {
            display: flex;
            flex-grow: 1;
            margin-right: 5px;
        }
    }
}

/* 03.Widths-Spacing */
.w-10 {
    width: 10% !important;
}

.w-90 {
    width: 90% !important;
}

.w-12 {
    width: 12% !important;
}

.w-88 {
    width: 88% !important;
}

.w-15 {
    width: 15% !important;
}

.w-85 {
    width: 85% !important;
}

.w-20 {
    width: 20% !important;
}

.w-80 {
    width: 80% !important;
}

.w-30 {
    width: 30% !important;
}

.w-70 {
    width: 70% !important;
}

.w-40 {
    width: 40% !important;
}

.w-60 {
    width: 60% !important;
}

.w-xs-100 {
    @include respond-below(sm) {
        width: 100% !important;
    }
}

.w-sm-100 {
    @include respond-below(md) {
        width: 100% !important;
    }
}

.r-0 {
    right: 0;
}

.l-0 {
    left: 0;
}

.depth-1 {
    @include depth(1);
}

.depth-2 {
    @include depth(2);
}

.min-width-zero {
    min-width: 0;
}

.mb-5,
.my-5 {
    margin-bottom: 2rem !important;
}

.float-none-xs {
    @include respond-below(xs) {
        float: initial !important;
    }
}

/* 04.Borders */
.border {
    border: 1px solid $separator-color-light !important;
}

.border-right {
    border-right: 1px solid $separator-color-light !important;
}

.border-left {
    border-left: 1px solid $separator-color-light !important;
}

.border-top {
    border-top: 1px solid $separator-color-light !important;
}

.border-bottom {
    border-bottom: 1px solid $separator-color-light !important;
}

.border-primary,
.border-theme-1 {
    border-color: $theme-color-1 !important;
}

.border-theme-2 {
    border-color: $theme-color-2 !important;
}

.border-theme-3 {
    border-color: $theme-color-3 !important;
}

.border-secondary {
    border-color: $primary-color !important;
}

.remove-last-border > *:last-child {
    border-bottom: initial !important;
}

.remove-last-margin > *:last-child {
    margin-bottom: initial !important;
}

.remove-last-padding > *:last-child {
    padding-bottom: initial !important;
}

/* 05.Badges */

.bg-theme-2,
.badge-theme-2 {
    background-color: $theme-color-2 !important;
    color: $button-text-color;
}

.bg-theme-3,
.badge-theme-3 {
    background-color: $theme-color-3 !important;
    color: $button-text-color;
}

.bg-primary,
.bg-theme-1,
.badge-primary,
.badge-theme-1 {
    background-color: $theme-color-1 !important;
    color: $button-text-color;
}

.bg-secondary,
.badge-secondary {
    background-color: $theme-color-2 !important;
    color: $button-text-color;
}

.bg-muted {
    background-color: $muted-color;
}

.bg-semi-muted {
    background-color: $background-color;
}

.badge-warning {
    background-color: $warning-color;
}

.badge-success {
    background-color: $success-color;
}

.badge-info {
    background-color: $info-color;
}

.badge-danger {
    background-color: $error-color;
}

.badge-success,
.badge-danger,
.badge-warning,
.badge-info {
    color: $button-text-color;
}

.badge {
    padding: 0.55em 0.75em 0.6em 0.75em;
    font-size: 74%;

    &.badge-pill {
        padding-right: 1.25em;
        padding-left: 1.25em;
    }

    &.badge-top-left {
        top: 10px;
        left: -7px;
    }

    &.badge-top-left-2 {
        top: 40px;
        left: -7px;
    }
    &.badge-top-left-3 {
        top: 70px;
        left: -7px;
    }

    &.badge-top-right {
        top: 8px;
        right: -7px;
    }

    &.badge-top-right-2 {
        top: 40px;
        right: -7px;
    }
}

.badge-light {
    background-color: $light-btn-background;
    color: $dark-btn-background;
}

.badge-dark {
    background-color: $dark-btn-background;
    color: $light-btn-background;
}

.badge-outline-primary,
.badge-outline-theme-1 {
    background: unset;
    border: 1px solid $theme-color-1;
    color: $theme-color-1;
}

.badge-outline-secondary,
.badge-outline-theme-2 {
    background: unset;
    border: 1px solid $theme-color-2;
    color: $theme-color-2;
}

.badge-outline-theme-3 {
    background: unset;
    border: 1px solid $theme-color-3;
    color: $theme-color-3;
}

.badge-outline-success {
    background: unset;
    border: 1px solid $success-color;
    color: $success-color;
}

.badge-outline-danger {
    background: unset;
    border: 1px solid $error-color;
    color: $error-color;
}

.badge-outline-warning {
    background: unset;
    border: 1px solid $warning-color;
    color: $warning-color;
}

.badge-outline-info {
    background: unset;
    border: 1px solid $info-color;
    color: $info-color;
}

.badge-outline-light {
    background: unset;
    border: 1px solid $light-btn-background;
    color: $light-btn-background;
}

.badge-outline-dark {
    background: unset;
    border: 1px solid $dark-btn-background;
    color: $dark-btn-background;
}

/* 06.Breadcrumb */
.breadcrumb-container {
    .breadcrumb {
        @include respond-below(md) {
            padding: 0;
        }
    }
}

.breadcrumb {
    background-color: transparent;
    margin-bottom: 0.5rem;
}

.breadcrumb-item + .breadcrumb-item::before {
    content: "|";
}

/* 07.Tabs */
.nav-tabs.separator-tabs {
    border-bottom: 1px solid $separator-color;
}

.nav-tabs .nav-link {
    border: initial;
    padding-top: 1rem;
    cursor: pointer;
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
    background: initial;
}

.nav-tabs .nav-link.active::before,
.nav-tabs .nav-item.show .nav-link::before {
    content: " ";
    background: $theme-color-1;
    border-radius: 10px;
    position: absolute;
    width: calc(100% - 1rem);
    height: 5px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

.nav-tabs.separator-tabs .nav-link.active::before,
.nav-tabs.separator-tabs .nav-item.show .nav-link::before {
    content: " ";
    background: $theme-color-1;
    border-radius: 10px;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    top: unset;
    transform: initial;
}

.nav-tabs.separator-tabs .nav-link {
    border: initial;
    padding-top: 1rem;
    background: initial;
    padding-left: 0;
    padding-top: 0.5rem;
    padding-right: 0;
    margin-right: 1.5rem;
    font-weight: 600;
    letter-spacing: 0.5px;
    color: $secondary-color;
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
    border: initial;
    position: relative;
    color: $theme-color-1;
}

.nav-tabs .nav-link:hover,
.nav-tabs .nav-link:focus {
    border: initial;
    color: $theme-color-1;
}

/* 08.Carousel */
.glide__slides {
    & > * {
        white-space: initial;
    }
}

.glide-item {
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 0.5rem;
}

.slider-nav {
    text-align: center;
    .left-arrow,
    .right-arrow {
        font-size: 20px;
        color: $theme-color-1;
        display: inline-block;
        vertical-align: middle;
        margin: 0 5px;
        padding-top: 14px;
    }

    .slider-dot-container {
        display: inline-block;
    }

    .btn {
        &:hover,
        &:focus,
        &:active {
            text-decoration: initial;
        }
    }
}

.slider-dot {
    width: 6px;
    height: 6px;
    border-radius: 10px;
    background: $separator-color;
    outline: initial !important;
    border: initial;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;

    &.glide__bullet--active {
        background: $theme-color-1;
    }
}

.glide__slides .card .card-body {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.glide__slides .card .w-50 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

/* 09.Navbar*/
.navbar {
    background: $foreground-color;
    height: $navbar-height;
    padding: 1.5rem 0;
    @include depth(1);

    #notificationDropdown {
        width: 260px;
        padding: 1.5rem !important;
        height: 280px;
        right: 15px;
    }

    #iconMenuDropdown {
        width: 240px;
        padding: 1.5rem !important;
        height: 280px;
    }

    .icon-menu-item {
        width: 90px;
        display: inline-block;
        text-align: center;
        margin-bottom: 1.7rem;
        color: rgba($primary-color, 0.8);

        i {
            font-size: 28px;
            line-height: 42px;
        }

        span {
            text-align: center;
            padding: 0 10px;
            line-height: 14px;
        }

        &:hover,
        &:focus {
            color: $theme-color-1;
        }
    }

    .menu-button-mobile {
        color: $secondary-color;
        text-align: center;
        margin-left: $main-margin-xs;
        svg {
            height: 12px;
            fill: $primary-color;
        }

        @include respond-below(sm) {
            width: 20px;
        }
    }

    .menu-button {
        color: $secondary-color;
        width: $main-menu-width;
        text-align: center;

        svg {
            height: 12px;
        }

        .main {
            fill: $primary-color;
            transition: fill $menu-collapse-time;
            width: 10px;
        }

        .sub {
            fill: $primary-color;
            transition: fill $menu-collapse-time;
        }

        &:hover {
            color: $theme-color-1;
        }

        @include respond-below(xl) {
            width: $main-menu-width-lg;
        }

        @include respond-below(lg) {
            width: $main-menu-width-md;
        }

        @include respond-below(sm) {
            width: 60px;
        }
    }

    .navbar-left {
        flex-basis: 40%;
    }

    .navbar-right {
        flex-basis: 40%;
        text-align: right;

        .rc-switch {
            margin-top: -2px;
        }

        .user {
            margin-right: $main-margin;

            @include respond-below(xl) {
                margin-right: $main-margin-lg;
            }

            @include respond-below(lg) {
                margin-right: $main-margin-md;
            }

            @include respond-below(sm) {
                margin-right: $main-margin-xs;
            }
        }
    }

    .navbar-logo {
        width: 110px;
        height: 35px;
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;

        .logo {
            width: 100%;
            height: 100%;
            background: url($logoPath) no-repeat;
            background-position: center center;
        }

        .logo-mobile {
            width: 100%;
            height: 100%;
            background: url($logoPathMobile) no-repeat;
            background-position: center center;
            background-size: contain;
        }

        @include respond-below(sm) {
            width: 80px;
        }

        @include respond-below(xxs) {
            width: 20px;
        }
    }

    .language-button {
        background: $background-color;
        border: initial;
        font-size: 0.8rem;
        color: $secondary-color;
        padding: 0.6rem 1rem;

        @include respond-below(xs) {
            padding-left: 0.75rem;
            padding-right: 0.75rem;
        }
    }

    .search {
        position: relative;
        width: $sub-menu-width;
        border-radius: 20px;
        background: $background-color;

        input {
            border: initial;
            background: transparent;
            outline: initial !important;
            padding: 0.5rem 1rem;
            line-height: 1.75;
            font-size: 0.8rem;
            width: 93%;
            color: $primary-color;
        }

        .search-icon {
            font-size: 17px;
            border-radius: 10px;
            color: $separator-color;
            position: absolute;
            width: 40px;
            height: 40px;
            bottom: -10px;
            right: 3px;
            text-align: center;
            cursor: pointer;

            &:hover {
                color: $theme-color-1;
            }
        }

        @include respond-below(lg) {
            width: $sub-menu-width * 0.7;
            input {
                width: 85%;
            }
        }

        @include respond-below(md) {
            width: $sub-menu-width/2;
            input {
                width: 85%;
            }
        }

        @include respond-below(sm) {
            width: 30px;
            height: 30px;
            background: initial;
            margin-left: 0.6rem;
            color: rgba($primary-color, 0.7);

            input {
                display: none;
            }

            .search-icon {
                font-size: 17px;
                width: 30px;
                height: 30px;
                bottom: -3px;
                right: 0;
                color: inherit;
            }

            &.mobile-view {
                display: block;
                width: 100%;
                position: fixed;
                z-index: 2;
                background: $foreground-color;
                left: 0;
                top: 0;
                height: $navbar-height-xs;
                margin-left: 15px;

                input {
                    display: block;
                    width: 100%;
                    height: 70px;
                    padding-left: 0;
                }

                span {
                    top: 50%;
                    transform: translateY(-50%);
                    right: 25px;
                }
            }
        }
    }

    .header-icons {
        margin-right: 1rem;

        @include respond-below(xs) {
            margin-right: 0;
        }
    }

    .header-icon {
        font-size: 16px;
        color: $secondary-color;
        padding-left: 0.6rem;
        padding-right: 0.6rem;
        vertical-align: initial;

        @include respond-below(xs) {
            padding-left: 0.3rem;
            padding-right: 0.3rem;
        }

        &:hover {
            color: $theme-color-1;
        }

        &#fullScreenButton i:last-of-type {
            display: none;
        }

        &.notificationButton {
            .count {
                font-size: 9px;
                color: $theme-color-1;
                border: 1px solid $theme-color-1;
                border-radius: 10px;
                position: absolute;
                width: 18px;
                height: 15px;
                text-align: center;
                font-weight: 700;
                top: -4px;
                right: 0px;
                line-height: 14px;

                @include respond-below(xs) {
                    right: -1px;
                }
            }
        }
    }

    .user {
        color: lighten($primary-color, 30%);
        position: relative;

        button {
            color: lighten($primary-color, 30%);
        }

        img {
            margin-left: 10px;
            border-radius: 30px;
            width: 40px;
        }

        @include respond-below(md) {
            .name {
                display: none;
            }
        }

        @include respond-below(sm) {
            img {
                width: 30px;
            }

            margin-left: initial;

            &:after {
                font-size: 11px;
                width: 14px;
                height: 14px;
                bottom: -3px;
                right: -3px;
            }
        }
    }

    @include respond-below(xl) {
        height: $navbar-height-lg;
    }

    @include respond-below(lg) {
        height: $navbar-height-md;
        padding: 1rem 0 1rem 0;
    }

    @include respond-below(sm) {
        height: $navbar-height-xs;
        padding: 10px 0;
    }
}

.sub-hidden .menu-button,
.menu-sub-hidden .menu-button {
    .sub {
        fill: $secondary-color;
    }
}

.main-hidden .menu-button,
.menu-hidden .menu-button {
    .main,
    .sub {
        fill: $secondary-color;
    }
}

/* 10.Menu*/
.sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    padding-top: $navbar-height;
    z-index: 4;
    height: calc(100% - #{$navbar-height});
    transition: border-radius $menu-collapse-time;

    .scrollbar-container {
        margin-right: 0;
        padding-right: 0;
    }

    .main-menu {
        width: $main-menu-width;
        height: calc(100% - #{$navbar-height});
        background: $foreground-color;
        z-index: 3;
        position: fixed;
        transition: transform $menu-collapse-time;
        padding-top: 10px;
        padding-bottom: 10px;
        left: 0;

        .scroll {
            padding-right: unset;
            margin-right: unset;
            height: 100%;

            .ps__thumb-y {
                right: 0;
            }
        }

        &.main-hidden {
            transform: translateX(-$main-menu-width);
        }

        ul li {
            position: relative;

            span {
                text-align: center;
                padding: 0 10px;
                line-height: 14px;
            }

            a {
                height: $main-menu-item-height;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                font-size: 13px;
                font-style: normal;
                font-weight: 400;
                border-bottom: 1px solid $separator-color-light;
                color: $primary-color;
                transition: color $menu-collapse-time;
                transition: background $menu-collapse-time;

                &:hover,
                &:focus {
                    color: $theme-color-1;
                }
            }

            i {
                font-size: 32px;
                line-height: 42px;
            }

            &.active a {
                color: $theme-color-1;
            }

            &.active:after {
                content: " ";
                background: $theme-color-1;
                border-radius: 10px;
                position: absolute;
                width: 6px;
                height: 90px;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
            }

            &:last-of-type {
                a {
                    border-bottom: initial;
                }
            }
        }

        @include respond-below(xl) {
            width: $main-menu-width-lg;
            height: calc(100% - #{$navbar-height-lg});
        }

        @include respond-below(lg) {
            width: $main-menu-width-md;
            height: calc(100% - #{$navbar-height-md});
        }

        @include respond-below(sm) {
            width: $main-menu-width-xs;
            height: calc(100% - #{$navbar-height-xs});

            ul li i {
                font-size: 28px;
                line-height: 38px;
            }

            ul li a {
                height: $main-menu-item-height-mobile;
            }

            ul li.active:after {
                width: 3px;
                height: 60px;
            }
        }
    }

    .sub-menu {
        width: $sub-menu-width;
        background: $foreground-color;
        z-index: 2;
        position: fixed;
        left: $main-menu-width;
        border-left: 1px solid $separator-color-light;
        @include depth(2);
        transition: transform $menu-collapse-time;
        height: calc(100% - #{$navbar-height});
        padding-top: 10px;
        padding-bottom: 10px;
        padding-right: 0;

        .scroll {
            margin-right: unset;
            padding-right: unset;
            height: 100%;

            .ps__thumb-y {
                right: 0;
            }

            .scrollbar-container {
                padding-top: 15px;
                padding-bottom: 15px;
            }
        }

        ul {
            display: none;

            li {
                margin-bottom: 0;
                margin-left: 30px;
                position: relative;

                a {
                    font-size: 13px;
                    display: block;
                    padding: 8px 0;
                    margin-bottom: 10px;
                }

                i {
                    font-size: 1.3em;
                    margin-right: 10px;
                    color: $secondary-color;
                    vertical-align: middle;
                    display: inline-block;
                }

                &.active i,
                &.active a {
                    color: $theme-color-1;
                }

                &.active:after {
                    content: " ";
                    background: $theme-color-1;
                    border-radius: 10px;
                    position: absolute;
                    width: 4px;
                    height: 4px;
                    top: 50%;
                    transform: translateY(-50%);
                    left: -16px;
                }

                span {
                    vertical-align: middle;
                    padding-top: 3px;
                    display: inline-block;
                }
            }

            li.has-sub-item {
                &.active:after {
                    content: " ";
                    background: initial;
                }
            }

            ul.third-level-menu {
                display: block !important;
                li {
                    a,
                    i {
                        color: $primary-color;
                    }
                    a:hover,
                    &.active i,
                    &.active a {
                        color: $theme-color-1;
                    }

                    &.active:after {
                        left: -22px;
                    }
                }
            }

            .rotate-arrow-icon {
                i {
                    transition: transform $animation-time-short;
                    transform: rotate(0);
                }

                &.collapsed {
                    i {
                        transform: rotate(-90deg);
                    }
                }
            }
        }

        @include respond-below(xl) {
            left: $main-menu-width-lg;
            width: $sub-menu-width-lg;
            height: calc(100% - #{$navbar-height-lg});
        }

        @include respond-below(lg) {
            left: $main-menu-width-md;
            width: $sub-menu-width-md;
            height: calc(100% - #{$navbar-height-md});
        }

        @include respond-below(sm) {
            left: $main-menu-width-xs;
            width: $sub-menu-width-xs;
            height: calc(100% - #{$navbar-height-xs});
        }
    }

    @include respond-below(xl) {
        padding-top: $navbar-height-lg;
        height: calc(100% - #{$navbar-height-lg});
    }

    @include respond-below(lg) {
        padding-top: $navbar-height-md;
        height: calc(100% - #{$navbar-height-md});
    }

    @include respond-below(sm) {
        padding-top: $navbar-height-xs;
        height: calc(100% - #{$navbar-height-xs});
        @include depth(1);
    }
}

//Menu Config Main Menu Sub Menu

.sub-hidden .sub-menu,
.menu-sub-hidden .sub-menu,
.menu-hidden .sub-menu {
    transform: translateX(-$sub-menu-width);

    @include respond-below(xl) {
        transform: translateX(-$sub-menu-width-lg);
    }

    @include respond-below(lg) {
        transform: translateX(-$sub-menu-width-md);
    }

    @include respond-below(sm) {
        transform: translateX(-$sub-menu-width-xs);
    }
}

.main-hidden .main-menu,
.menu-hidden .main-menu {
    transform: translateX(-$main-menu-width);

    @include respond-below(xl) {
        transform: translateX(-$main-menu-width-lg);
    }

    @include respond-below(lg) {
        transform: translateX(-$main-menu-width-md);
    }

    @include respond-below(sm) {
        transform: translateX(-$main-menu-width-xs);
    }
}

.main-hidden.sub-hidden .sub-menu,
.menu-hidden .sub-menu {
    transform: translateX(-$sub-menu-width - $main-menu-width);

    @include respond-below(xl) {
        transform: translateX(-$sub-menu-width-lg - $main-menu-width-lg);
    }

    @include respond-below(lg) {
        transform: translateX(-$sub-menu-width-md - $main-menu-width-md);
    }

    @include respond-below(sm) {
        transform: translateX(-$sub-menu-width-xs - $main-menu-width-xs);
    }
}

//Mobile
.menu-mobile {
    .main-menu {
        transform: translateX(-$main-menu-width-xs);
    }

    .sub-menu {
        transform: translateX(-$sub-menu-width-xs - $main-menu-width-xs - 50);
    }
}

//Show Temporary
.main-show-temporary {
    .main-menu {
        transform: translateX(0);
    }

    .sub-menu {
        transform: translateX(-$sub-menu-width);
    }

    @include respond-below(xl) {
        .main-menu {
            transform: translateX(0);
        }

        .sub-menu {
            transform: translateX(-$sub-menu-width-lg);
        }
    }

    @include respond-below(lg) {
        .main-menu {
            transform: translateX(0);
        }

        .sub-menu {
            transform: translateX(-$sub-menu-width-md);
        }
    }

    @include respond-below(sm) {
        .sub-menu {
            transform: translateX(-$sub-menu-width-xs);
        }
    }
}

.sub-show-temporary,
.menu-mobile.sub-show-temporary {
    .sub-menu {
        transform: translateX(0);
    }
}

/* 11.Apps*/
.app-menu {
    z-index: 1;
    @include depth(1);
    width: $app-menu-width;
    float: right;
    background: $foreground-color;
    transition: transform $menu-collapse-time;
    height: calc(100% - #{$navbar-height});
    position: fixed;
    right: 0;
    top: $navbar-height;

    @include respond-below(xl) {
        top: $navbar-height-lg;
        height: calc(100% - #{$navbar-height-lg});
    }

    @include respond-below(lg) {
        top: $navbar-height-md;
        transform: translateX($app-menu-width);
        height: calc(100% - #{$navbar-height-md});
    }

    @include respond-below(sm) {
        top: $navbar-height-xs;
        height: calc(100% - #{$navbar-height-xs});
    }

    .scrollbar-container {
        margin-right: unset;
        padding-right: unset;

        .ps__rail-y {
            right: 2px !important;
        }
    }

    &.shown {
        transform: translateX(0);
    }

    .app-menu-button {
        cursor: pointer;
        position: absolute;
        left: -28px;
        background: $foreground-color;
        top: $main-margin - 15;
        padding: 12px 8px 12px 5px;
        border-radius: 0.2rem;
        color: $secondary-color !important;
        box-shadow: -2px 0px 5px rgba(0, 0, 0, 0.04);
        font-size: 15px;
        line-height: 0;

        @include respond-below(xl) {
            top: $main-margin-lg - 14;
        }

        @include respond-below(lg) {
            top: $main-margin-md - 13;
        }

        @include respond-below(sm) {
            top: $main-margin-xs - 2;
        }
    }

    ul {
        li {
            margin-bottom: 5px;

            a {
                font-size: 13px;
                display: block;
                padding: 3px 0;

                &:hover i {
                    color: $theme-color-1;
                }
            }

            i {
                font-size: 1.2em;
                margin-right: 10px;
                color: $secondary-color;
                vertical-align: unset;

                @include respond-below(sm) {
                    font-size: 20px;
                }
            }

            &.active i,
            &.active a {
                color: $theme-color-1;
            }
        }
    }
}

.app-row {
    padding-right: $app-menu-width;

    @include respond-below(lg) {
        padding-right: 0;
    }
}

/* 12.Chat App*/
.chat-app {
    .scrollbar-container {
        padding-left: 10px;
        margin-left: -10px;
        padding-right: 10px;
        margin-right: 0;
        height: calc(100vh - #{$navbar-height} - 270px);

        @include respond-below(xl) {
            height: calc(100vh - #{$navbar-height-lg} - 270px);
        }

        @include respond-below(lg) {
            height: calc(100vh - #{$navbar-height-md} - 270px);
        }

        @include respond-below(sm) {
            height: calc(100vh - #{$navbar-height-xs} - 220px);
        }

        .ps__thumb-y {
            right: 10px;
        }
    }

    .chat-text-left {
        padding-left: 64px;
    }

    .chat-text-right {
        padding-right: 64px;
    }

    .list-item-heading {
        @include respond-below(sm) {
            font-size: 0.9rem;
        }
    }

    .card .card-body {
        @include respond-below(sm) {
            padding: 0.75rem;
        }
    }
}

.chat-app-tab-content {
    height: calc(100% - 45px);
}

.chat-app-tab-pane {
    height: calc(100% - 45px);
}

.chat-input-container {
    width: 100%;
    height: 90px;
    background: $input-background;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    @include depth(1);
    transition: padding $menu-collapse-time;
    padding-left: $main-menu-width + $sub-menu-width + $main-margin;
    padding-right: $app-menu-width + $main-margin;

    @include respond-below(xl) {
        padding-left: $main-menu-width-lg + $main-margin-lg;
        padding-right: $app-menu-width + $main-margin-lg;
    }

    @include respond-below(lg) {
        padding-left: $main-menu-width-md + $main-margin-md;
        padding-right: $main-margin-md;
    }

    @include respond-below(sm) {
        padding-right: 15px;
        padding-left: 15px;
    }

    input {
        border: initial;
        width: unset;
        height: 90px;
        padding-left: 0;
        display: inline-block;
    }

    .send-button {
        @include respond-below(md) {
            padding-left: 0.7rem;
            padding-right: 0.7rem;
        }
    }
}

.menu-sub-hidden,
.sub-hidden {
    .chat-input-container {
        padding-left: $main-menu-width + $main-margin;

        @include respond-below(xl) {
            padding-left: $main-menu-width-lg + $main-margin-lg;
        }

        @include respond-below(lg) {
            padding-left: $main-menu-width-md + $main-margin-md;
        }

        @include respond-below(sm) {
            padding-right: 15px;
            padding-left: 15px;
        }
    }
}

.menu-main-hidden,
.menu-hidden,
.main-hidden {
    .chat-input-container {
        padding-left: $main-margin;

        @include respond-below(xl) {
            padding-left: $main-margin-lg;
        }

        @include respond-below(lg) {
            padding-left: $main-margin-md;
        }

        @include respond-below(sm) {
            padding-right: 15px;
            padding-left: 15px;
        }
    }
}

/* 13.Survey App*/
.survey-app {
