}

.custom-select {
    border-radius: $border-radius;
    padding: 0.75rem 0.75rem 0.5rem 0.75rem;
}

.input-group > .form-control:not(:first-child),
.input-group > .custom-select:not(:first-child) {
    outline: initial !important;
    box-shadow: initial !important;
}

.custom-select {
