}

.custom-select:focus {
    border-color: $theme-color-1;
}

.custom-file-input:focus ~ .custom-file-label {
    border-color: rgba($theme-color-1, 0.6);
}

.custom-file-label::after {
    background: $foreground-color;
    color: $primary-color;
    border-color: $separator-color;
}

.custom-file-input {
