}

/* 22.Slider(Range) */
.rc-slider-tooltip {
    background: $input-background !important;
    color: $primary-color !important;
    border: 1px solid $separator-color !important;
    border-radius: $border-radius !important;
    text-align: center !important;
    top: 150% !important;
    bottom: unset !important;
    padding: 5px !important;
    transform: translateX(-50%) !important;
}

.rc-slider-handle {
    width: 20px !important;
    height: 20px !important;
    border-radius: 20px !important;
    background: $foreground-color !important;
    cursor: default !important;
    border: 1px solid $separator-color !important;
    box-shadow: initial !important;
}

.rc-slider-track,
.rc-slider-rail,
.rc-slider-step {
    height: 7px !important;
}

.rc-slider-rail {
    border: 1px solid $separator-color !important;
    background: $foreground-color !important;
}

.rc-slider-handle {
    margin-top: -7px !important;
}

.rc-slider-track {
    background: $theme-color-1 !important;
}

/* 23.Navs */
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
    color: $foreground-color;
    background-color: $theme-color-1;
}

.nav-pills .nav-link {
    border-radius: 50px;
}

.nav-link {
    padding: 0.5rem 1rem;

    @include respond-below(sm) {
        padding: 0.5rem 0.5rem;
    }

    &.active {
        color: $theme-color-1;
    }
}

.page-item {
    padding: 10px;

    @include respond-below(sm) {
        padding: 3px;
    }

    .page-link {
        outline: initial !important;
        box-shadow: initial !important;
        line-height: 1.7;
        border-radius: 40px !important;
        min-width: 38px;
        text-align: center;
        height: 38px;
        padding: 0.55rem 0;

        @include respond-below(xs) {
            min-width: 30px;
            height: 30px;
            line-height: 0.9;
            font-size: 0.76rem;
        }

        &.next {
            background: $theme-color-1;
            color: $foreground-color;
            border: 1px solid $theme-color-1;
        }

        &.prev,
        &.prev {
            background: $theme-color-1;
            border: 1px solid $theme-color-1;
            color: $foreground-color;
        }

        &.first,
        &.last {
            background: transparent;
            color: $theme-color-1;
            border: 1px solid $theme-color-1;
            border-radius: 30px;

            &:hover {
                background: $theme-color-1;
                color: $foreground-color;
                border: 1px solid $theme-color-1;
            }
        }

        &:hover {
            background-color: transparent;
            border-color: lighten($theme-color-1, 10%);
            color: $theme-color-1;
        }
    }

    &.active .page-link {
        background: transparent;
        border: 1px solid $theme-color-1;
        color: $theme-color-1;
    }

    &.disabled .page-link {
        border-color: $separator-color;
        color: $separator-color;
        background: transparent;
    }
}

.page-link {
    background-color: transparent;
    border-color: transparent;
    color: $primary-color;
}

.btn-sm.page-link {
    padding: 0.5rem 0.5rem;
}

.pagination-lg {
    .page-item {
        padding: 15px;

        @include respond-below(sm) {
            padding: 3px;
        }

        .page-link {
            min-width: 50px;
            height: 50px;

            @include respond-below(sm) {
                min-width: 38px;
                height: 38px;
                padding: 0.55rem 0;
                line-height: 1.5;
                font-size: 0.8rem;
            }

            @include respond-below(xs) {
                min-width: 30px;
                height: 30px;
                line-height: 0.9;
                font-size: 0.76rem;
            }
        }
    }
}

.pagination-sm {
    .page-item {
        padding: 6px;

        @include respond-below(sm) {
            padding: 3px;
        }

        .page-link {
            min-width: 30px;
            height: 30px;
            line-height: 0.9;
            font-size: 0.76rem;
        }
    }
}

/* 24.Tooltip and popover */
.popover,
.tooltip {
    top: -145px !important;

    @include respond-below(xl) {
        top: -130px !important;
    }

    @include respond-below(lg) {
        top: -110px !important;
    }

    @include respond-below(sm) {
        top: -85px !important;
    }
}

.popover {
