    color: $primary-color;
    background-color: $foreground-color;
    border-radius: 0.1rem;
    border: 1px solid $separator-color;
}

.tooltip.show {
    opacity: 1;
}

.bs-popover-right .arrow::before,
.bs-popover-auto[x-placement^="right"] .arrow::before {
