/* 27.Sortable */
.sortable {
    cursor: default;
}

.sortable span {
    vertical-align: middle;
}

.sortable-ghost {
    opacity: 0.5;
}

/* 28.Spinner */
.btn-multiple-state {
    .spinner {
        width: 36px;
        text-align: center;
    }

    .spinner > span {
        width: 6px;
        height: 6px;
        background-color: $button-text-color;
        border-radius: 100%;
        display: inline-block;
        -webkit-animation: sk-bouncedelay 1.2s infinite ease-in-out both;
        animation: sk-bouncedelay 1.2s infinite ease-in-out both;
    }

    .spinner .bounce1 {
        -webkit-animation-delay: -0.32s;
        animation-delay: -0.32s;
    }

    .spinner .bounce2 {
        -webkit-animation-delay: -0.16s;
        animation-delay: -0.16s;
    }
}

@-webkit-keyframes sk-bouncedelay {
    0%,
    80%,
    100% {
        -webkit-transform: scale(0);
    }

    40% {
        -webkit-transform: scale(1);
    }
}

@keyframes sk-bouncedelay {
    0%,
    80%,
    100% {
        -webkit-transform: scale(0);
        transform: scale(0);
    }

    40% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

/* 29.Invoice Print */
.invoice-react {
    .invoice-contents {
        background-color: $foreground-color;
        height: 1200px;
        max-width: 830px;
        position: relative;
    }
}

@media print {
    body {
        background: $foreground-color;
        height: 100%;
    }

    main {
        margin: 0 !important;
    }

    .navbar,
    .sidebar,
    .theme-colors {
        display: none;
    }

    main .container-fluid .row:not(.invoice) {
        display: none;
    }

    .invoice {
        width: 100%;
        max-width: 100%;

        &.row {
            margin: 0;
        }

        [class*="col"] {
            padding: 0;
        }

        .invoice-contents {
            width: 100%;
            max-width: 100% !important;
            height: 1370px !important;
        }
    }
}

/* 30.Modal */
.modal {
    .modal-header,
    .modal-body,
    .modal-footer {
        padding: 1.75rem;
    }

    .modal-header {
        border-bottom: 1px solid $separator-color;
    }

    .modal-footer {
        border-top: 1px solid $separator-color;
    }

    .close {
        color: $primary-color;
        text-shadow: initial;
    }
}

.modal-content {
    border: initial;
    border-radius: 0.1rem;
    background: $foreground-color;
}

.modal-dialog {
    @include depth(2);
}

.modal-right {
    padding-right: 0 !important;

    .modal-dialog {
        margin: 0 auto;
        margin-right: 0;
        margin-top: 0;
        margin-bottom: 0;
        height: 100%;
        max-width: 380px;
    }

    .modal-content {
        min-height: 100%;
    }

    .modal-header {
        height: $navbar-height;

        @include respond-below(xl) {
            height: $navbar-height-lg;
        }

        @include respond-below(lg) {
            height: $navbar-height-md;
        }

        @include respond-below(sm) {
            height: $navbar-height-xs;
        }
    }

    .modal-footer {
        justify-content: center;
    }

    .modal.fade .modal-dialog {
        transform: translate(25%, 0) !important;
    }

    .modal.show .modal-dialog {
        transform: translate(0, 0) !important;
    }
}

/* 31.Authorization */
.auth-card {
    display: flex;
    flex-direction: row;
    @include depth(1);

    .image-side {
        width: 40%;
        background: url("/assets/img/login-balloon.jpg") no-repeat center top;
        background-size: cover;
        padding: 80px 40px;

        .h3 {
            line-height: 0.8rem;
        }
    }

    .form-side {
        width: 60%;
        padding: 80px;
    }

    @include respond-below(md) {
        flex-direction: column;

        .image-side {
            width: 100%;
            padding: 60px;
        }

        .form-side {
            width: 100%;
            padding: 60px;
        }
    }

    @include respond-below(sm) {
        p.h2 {
            font-size: 1.6rem;
        }
    }

    @include respond-below(xs) {
        flex-direction: column;

        .image-side {
            padding: 35px 30px;
        }

        .form-side {
            padding: 35px 30px;
        }

        .logo-single {
            margin-bottom: 20px;
        }

        p.h2 {
            font-size: 1.4rem;
        }
    }
}

